<template>
    <div class="PersonInf">
      <Header></Header>
      <br>

      <div style="text-align:center;">
        <img :src="headPath" alt="" style="width:150px;height:150px;border-radius:75px;">
      </div>
      <!-- <input type="file" enctype="" accept="image/*" @change="ChangePic"> -->
      <div>
        <v-file-input
          style="width:25px;margin:0 auto;"
          label="点击上传头像"
          clearable
          hide-input
          prepend-icon="mdi-camera"
          v-model="headPic"
          @change="ChangePic"
        ></v-file-input>
      </div>

      <div style="margin: 0 auto; width:700px">
        <h2 style="margin-left:1em; margin-top:1em">用户信息</h2>
        <br>
        <v-card class="pi" elevation="4">
          <br>
           <!-- <v-btn @click="ChangePhoneinf" style="float: right" class="mx-2" fab dark color="cyan" x-small depressed><v-icon dark>mdi-pencil</v-icon></v-btn> -->
          <v-card-text style="font-size: 15px">用户名: {{UserName}}<v-btn @click="ChangeNameinf" style="float: right" class="mx-2" fab dark color="cyan" x-small depressed><v-icon dark>mdi-pencil</v-icon></v-btn></v-card-text>
          <v-card-text style="font-size: 15px">账号ID: {{UserId}}</v-card-text>
          <v-card-text style="font-size: 15px">手机号码: {{UserPhone}}</v-card-text>
          <v-card-text style="font-size: 15px">密码: {{UserPassword}}<v-btn @click="ChangePasswordinf" style="float: right" class="mx-2" fab dark color="cyan" x-small depressed><v-icon dark>mdi-pencil</v-icon></v-btn></v-card-text>
          <br>
        </v-card>

        <v-card class="changeinf" elevation="15" v-show="disall === true">
          <v-btn style="float:right; margin-top:1em;" class="mx-2" fab dark color="red" x-small depressed @click="close"><v-icon dark>mdi-close-thick</v-icon></v-btn>
          <br>
          <br>
          <v-text-field
              label="请输入新的用户名"
              hide-details="auto"
              style="width:500px; margin: 0 auto"
              v-model="newUserName"
              v-bind:disabled="dis1"
          ></v-text-field>
          <br>
          <v-text-field
              label="请输入新的电话号码"
              hide-details="auto"
              style="width:500px; margin: 0 auto"
              v-model="newUserPhone"
              v-bind:disabled="dis2"
          ></v-text-field>
          <br>
          <v-text-field
              label="请输入新的密码"
              hide-details="auto"
              style="width:500px; margin: 0 auto"
              v-model="newUserPassword"
              v-bind:disabled="dis3"
          ></v-text-field>
          <br>
          <v-card-actions style="margin: 0 auto; width:400px">
            <v-btn @click="cancelChanges">放弃修改</v-btn>
            <v-btn style="margin-left: 50%" @click="submitChanges">提交修改</v-btn>
          </v-card-actions>
        </v-card>
      </div>
    </div>
</template>

<script>
import "@/assets/font/font.css"
import Header from '@/components/Header'
import axios from 'axios'
export default {
  name: 'PersonalCenter',
  components: {
    Header,
  },
  data (){
    return {
      UserName: "",
      UserId: "1",
      UserPhone: "",
      UserPassword: "",
      newUserName: "",
      newUserPhone: "",
      newUserPassword: "",
      dis1: true,
      dis2: true,
      dis3: true,
      disall: false,
      headPic: null,
      uploadPath: '',
      headPath: '',
      userInfo: {
        avatar: require("../assets/manage/all.png")
      }
    }
  },
  mounted(){
    this.$store.state.router = 2;
    this.UserId=localStorage.getItem('id')
    this.getInf();
  },
  methods: {

    getInf() {
      this.$axios
          .post("/personalcenter1", {
            UserId: this.UserId,
          })
          .then((response) => {
            console.log(response);
            this.UserName = response.data.UserName;
            this.UserPassword = response.data.UserPassword;
            this.UserPhone = response.data.UserPhone;
            this.headPath = 'http://101.35.86.15:8080/image/'
            this.headPath += response.data.path.slice(13);
            this.uploadPath = response.data.path;
          })
          .catch(function (error) {
            console.log(error);
          });
    },
    close(){
      this.disall = false;
    },
    ChangeNameinf(){
      this.newUserName = this.UserName;
      this.newUserPhone = this.UserPhone;
      this.newUserPassword = this.UserPassword;
      this.disall = true;
      this.dis1 = false;
      this.dis2 = true;
      this.dis3 = true;
    },
    ChangePhoneinf(){
      this.newUserName = this.UserName;
      this.newUserPhone = this.UserPhone;
      this.newUserPassword = this.UserPassword;
      this.disall = true;
      this.dis2 = false;
      this.dis1 = true;
      this.dis3 = true;
    },
    ChangePasswordinf(){
      this.newUserName = this.UserName;
      this.newUserPhone = this.UserPhone;
      this.newUserPassword = this.UserPassword;
      this.disall = true;
      this.dis3 = false;
      this.dis2 = true;
      this.dis1 = true;
    },
    submitChanges(){
      if(this.dis2 == false){
        if(this.validateUserPhone(this.newUserPhone) != true){
          alert("新的用户号码不合法,请重新填写");
          this.newUserPhone = "";
        }
        else{
          this.HandOver();
        }
      }
      else{
        this.HandOver();
      }
    },
    cancelChanges(){
      alert("已编辑内容将不会保存!  是否放弃?");
      this.newUserName = "";
      this.newUserPassword = "";
      this.newUserPhone = "";
      this.disall = false;
      this.dis1 = true;
      this.dis2 = true;
      this.dis3 = true;
    },
    validateUserPhone(value) {
      var pattern1 = /^1[3456789]\d{9}$/;
      var res1 = pattern1.test(value);
      if ((res1) === false) {

        return "电话不符合格式";
      } else {
        return true;
      }
    },
    Update(){
      this.disall = false;
      this.dis1 = true;
      this.dis2 = true;
      this.dis3 = true;
    },
    HandOver(){
      this.$axios
          .post("/personalcenter2", {
            UserId: this.UserId,
            newUserName: this.newUserName,
            newUserPassword: this.newUserPassword,
            newUserPhone : this.newUserPhone,
            path: this.uploadPath,
          })
          .then((response) => {
            console.log(response);
            if (response.data.isSuccessed == true) {
              this.$message.success("修改个人信息成功");
              this.UserPassword = response.data.newUserPassword;
              this.UserName = response.data.newUserName;
              this.$store.state.UserName=response.data.newUserName;
              this.UserPhone= response.data.newUserPhone;
              this.Update(response);
            } else {
              console.log(response.data.message)
              this.$message.error("修改个人信息失败");
              this.newUserName = "";
              this.newUserPassword = "";
              this.newUserPhone = "";
              this.disall = false;
              this.dis1 = true;
              this.dis2 = true;
              this.dis3 = true;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
     },
    ChangePic(){
        var formData = new FormData();
        formData.append("file", this.headPic);
        formData.append("id", this.UserId);
        // 上传头像
        axios({
          url: '/personalcenter/changepic',
          method: 'post',
          async: false,
          data: formData,
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(res => {
          this.headPath = 'http://101.35.86.15:8080/image/'
          this.headPath += res.data.path.slice(13);
        }).catch(res => {
          console.log(res);
        })
    },
  }
}
</script>

<style scoped>
.bgColor {
    background-color: rgb(247, 247, 247);
}
.p1{
  margin: 0 auto;
}
.PersonInf{
  margin: 0 auto;
  position: relative;
  width:100%;
  background-color: rgb(247, 247, 247);
  height: 100%;
}
.pi{
  position: relative;
  width:700px;
}
.changeinf{
  margin-top:1em;
  position:relative;
  width: 700px
}
</style>